export class RadixConvert {
    private static exponent: number = 34;
    private static keys: string = "0123456789ABCDEFGHJKLMNPQRSTUVWXYZ";

    public static Decimal2String(value: number): string {
        let result: string = "";
        do {
            let index: number = value % this.exponent;
            result = this.keys[index] + result;
            value = (value - index) / this.exponent;
        }
        while (value > 0);

        return result;
    }

    private static Pow(baseNo: number, x: number): number {
        let value: number = 1;
        while (x > 0) {
            value = value * baseNo;
            x--;
        }

        return value;
    }
}